<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="product_detail pages position-relative overflow-hidden">
    <div class="container position-relative z-index-1 box_">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link to="/products" itemprop="item" title="商品介紹">
              <span itemprop="name">商品介紹</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link :to="`/products/${product.category}`" itemprop="item" :title="product.category">
              <span itemprop="name">{{ product.category }}</span>
            </router-link>
            <meta itemprop="position" content="3" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link :to="`/product/${product.id}`" itemprop="item" :title="product.title">
              <span itemprop="name">{{ product.title }}</span>
            </router-link>
            <meta itemprop="position" content="4" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white p-lg-5 px-4 py-4 mb-4">
        <form action="" method="post">
          <div class="row">
            <div class="product-img col-lg-6 mb-4 mb-lg-0">
              <div class="show-box-wrap overflow-hidden position-relative">
                <swiper :modules="[Thumbs]" :thumbs="{ swiper: thumbsSwiper }" :spaceBetween="30">
                  <swiper-slide>
                    <img :src="product.imageUrl" class="w-100"/>
                  </swiper-slide>
                  <swiper-slide v-for="item in product.imagesUrl" :key="item">
                    <img :src="item" class="w-100"/>
                  </swiper-slide>
                </swiper>
              </div>
              <div
                class="thumbsSlider_out position-relative mt-3 overflow-hidden"
              >
                <swiper :modules="[Thumbs]" watch-slides-progress @swiper="setThumbsSwiper" :spaceBetween="10" :slidesPerView="3">
                  <swiper-slide>
                    <img :src="product.imageUrl" class="w-100" />
                  </swiper-slide>
                  <swiper-slide v-for="item in product.imagesUrl" :key="item">
                    <img :src="item" class="w-100" />
                  </swiper-slide>
                </swiper>
              </div>
            </div>
            <div class="product-info col-lg mb-4 mb-lg-0 pl-lg-4">
              <h3 class="mb-4">【{{ product.title }}】</h3>
              <div class="info_box pt-4 pb-xl-5 pb-3 mb-2">
                <p>
                  {{ product.description }}
                </p>
                <p>
                  {{ product.content }}
                </p>
              </div>
              <div class="price-box col-12 mb-4">
                <span class="old-price me-3">原價 $ 150</span>
                <span>限量優惠 $<span class="price">250</span>元</span>
              </div>
              <div class="row">
                <div class="col-sm-4 mb-4 mb-lg-1 pr-xl-4 pr-sm-0">
                  <div class="count">
                    <div class="row mx-0">
                      <button
                        class="col-auto value-button decrease_ btn btn-primary"
                        id=""
                        value="Decrease Value"
                        @click.prevent="qty -= 1"
                        :disabled="qty === 1"
                      >
                        <svg-icon name="less"/>
                      </button>
                      <input
                        class="col qty px-1"
                        min="1"
                        type="number"
                        onkeyup="value=value.replace(/[^\d]+/g,'')"
                        v-model.number="qty"
                      />
                      <button
                        class="col-auto value-button increase_"
                        id=""
                        value="Increase Value"
                        @click.prevent="qty += 1"
                      >
                        <svg-icon name="add"/>
                      </button>
                    </div>
                  </div>
                </div>
                <div class="col-sm mb-4 mb-lg-1 btn_box">
                  <div class="row mx-n2">
                    <a
                      href=""
                      class="col px-1 mx-2 btn btn-outline-primary d-flex align-items-center justify-content-center"
                      :class="{'disabled': loadingItem}"
                      @click.prevent="addCart()"
                      >
                      <div v-if="loadingItem" class="spinner-border text-warning me-xl-2 me-1" role="status">
                        <span class="visually-hidden">Loading...</span>
                      </div>
                      <svg-icon name="cart" class="svg me-xl-2 me-1" :class="{'d-none' : loadingItem}"/>
                      加入購物車</a
                    >
                    <button class="btn btn-primary col px-1 mx-2" @click.prevent="goCart()">
                      直接購買
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 pt-4 pb-2">
        <h4 class="text-center mb-4 pt-3"><span>看看其它產品</span></h4>
        <ProductList :list="other" :page="currentPage" @prd-link="changLink"></ProductList>
      </div>
    </div>
  </section>
</template>
<script>
import { ref } from 'vue'
import { Thumbs } from 'swiper'
import { Swiper } from 'swiper/vue/swiper'
import { SwiperSlide } from 'swiper/vue/swiper-slide'
import 'swiper/swiper.min.css'
import pagesBanner from '../components/pagesBanner.vue'
import ProductList from '../components/ProductList.vue'
import emitter from '../methods/emitter'
export default {
  components: {
    pagesBanner,
    Swiper,
    SwiperSlide,
    ProductList
  },
  data () {
    return {
      isLoading: false,
      loadingItem: false,
      product: {},
      id: '',
      other: [],
      qty: 1,
      currentPage: 1
    }
  },
  watch: {
    qty () {
      if (this.qty < 1) {
        this.qty = 1
      }
    },
    id () {
      this.qty = 1
    }
  },
  methods: {
    getData () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/product/${this.id}`
      this.$http.get(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          this.product = res.data.product
        }
      })
    },
    getOther () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          this.other = res.data.products
          this.randomData()
        }
      })
    },
    randomData () {
      var result = []
      var ranNum = 3
      const vm = this
      for (var i = 0; i < vm.other.length; i++) {
        var ran = Math.floor(Math.random() * (vm.other.length - i))
        result.push(vm.other[ran])
        vm.other[ran] = vm.other[vm.other.length - i - 1]
      }
      const newRecords = []
      result.forEach((item, i) => {
        if (i % ranNum === 0) {
          newRecords.push([])
        }
        const page = parseInt(i / ranNum)
        newRecords[page].push(item)
      })
      this.other = newRecords.slice(0, 1)
    },
    simpleScrollTrigger () {
      this.gsap.from('.box_', {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.5,
        scrollTrigger: {
          trigger: '.box_',
          start: 'top 90%'
        }
      })
    },
    changLink (newLink) {
      this.id = newLink
      this.getData()
      this.getOther()
    },
    addCart () {
      this.loadingItem = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      const cart = {
        product_id: this.id,
        qty: this.qty
      }
      this.$http.post(api, { data: cart }).then((res) => {
        if (res.data.success) {
          this.loadingItem = false
          emitter.emit('getData')
          this.$httpMessage(res, '加入購物車')
        }
      })
    },
    goCart () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      const cart = {
        product_id: this.id,
        qty: this.qty
      }
      this.$http.post(api, { data: cart }).then((res) => {
        if (res.data.success) {
          emitter.emit('getData')
          this.$router.push('/cart')
        }
      })
    }
  },
  created () {
    this.id = this.$route.params.productId
    this.getData()
    this.getOther()
  },
  mounted () {
    this.simpleScrollTrigger()
  },
  setup () {
    const thumbsSwiper = ref(null)
    const setThumbsSwiper = (swiper) => {
      thumbsSwiper.value = swiper
    }
    return {
      Thumbs,
      thumbsSwiper,
      setThumbsSwiper
    }
  }
}
</script>
